#root, body, html {
  height: 100%;
}

.sectionlist-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  padding-top: 20px;
}

.section-container {
  /* margin-bottom: 10px; */
}

.section-header {
  background-color: #ffffff;
  display: flex;
  align-items: flex-start;
  padding-left:16px;
}
.section-description {
  display: flex;
  padding-left:16px;
  font-family:"Poppins";
  color: #ffffff;
  font-size: 8px;
}

.section-item-container {
  background-color: white;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}

.section-item {
  display: flex;
  align-items: center;
  /* height: 45px; */
  padding-left: 16px;
  border-bottom: 1px solid #eee;
}

.section-item:last-child {
  border-bottom: none;
}